import React, { useEffect, useState } from "react";
import axios from "axios";
import "./Homepage.scss";
import MyModal from "./MyModal";
import Container from "@mui/material/Container";
import SearchComponent from "./Search";
import {
  CardContent,
  Alert,
  AlertTitle,
  CardMedia,
  Typography,
  CardActionArea,
  Card,
  Button,
  TextField,
  Pagination,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";

const API_KEY = "85a17fbd";

const HomePage = () => {
  const [filmName, setFilmName] = useState("Avengers");
  const [inputValue, setInputValue] = useState("");
  const [data, setData] = useState("");
  const [dataFull, setDataFull] = useState("");
  const [update, setUpdate] = useState(false);
  const [updateFull, setUpdateFull] = useState(false);
  const [loading, setLoading] = useState(true);
  const [errorMsg, setErrorMsg] = useState(null);
  const [errorYearMsg, setYearErrorMsg] = useState(false);
  const [page, setPage] = useState(1);
  const [maxPage, setMaxPage] = useState(4);
  const [year, setYear] = useState("");
  const [type, setType] = useState("");
  const [yearInputValue, setYearInputValue] = useState("");
  const [imdbID, setimdbID] = useState(null);
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleSubmitForm = (e) => {
    e.preventDefault();
    setFilmName(inputValue.toLowerCase());

    if (yearInputValue) {
      if (yearInputValue > 1980) {
        setYear(yearInputValue);
        setUpdate(!update);
        setYearErrorMsg(false);
        setPage(1);
      } else {
        setYearErrorMsg(true);
      }
    } else {
      setYear("");
      setPage(1);
      setUpdate(!update);
    }
  };
  useEffect(() => {
    if (imdbID) {
      axios
        .get(`http://www.omdbapi.com/?apikey=${API_KEY}&i=${imdbID}&plot=full`)
        .then((ta) => {
          if (ta.status === 200) {
            console.log(ta.data);
            console.log(imdbID);
            setDataFull(ta.data);
          }
        });
    } else console.log("else");
  }, [updateFull]);

  useEffect(() => {
    const wrongSearch = [
      {
        imdbID: Date.now(),
        Poster: "",
        Title: "not found",
        Type: "",
        Year: "",
      },
    ];

    axios
      .get(
        `http://www.omdbapi.com/?apikey=${API_KEY}&s=${filmName}&t=${filmName}&page=${page}&type=${type}&y=${year}`
      )
      .then((data) => {
        if (data.status === 200) {
          if (data.data.Response === "True") {
            setData(data.data.Search);
            setMaxPage(Math.floor(Number(data.data.totalResults) / 10) + 1);
            setLoading(false);
          } else setData(wrongSearch);
          setErrorMsg(data.data.Error);
        } else throw "bad request";
      })
      .catch((err) => {
        console.error(err);
        setErrorMsg("Something went wrong");
        setLoading(false);
      });
  }, [update, page]);

  const handlePageChange = (event, value) => {
    setPage(value);
  };
  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };
  const handleYearInputChange = (e) => {
    setYearInputValue(e.target.value);
  };
  const handleChangeType = (e) => {
    setType(e.target.value);
  };
  const clickFunction = (e, name) => {
    setUpdateFull(!updateFull);
    setimdbID(name);
    setTimeout(() => handleOpen(), 250);
  };
  return (
    <div className="wrapper">
      <header className={"header"}>
        <form className={"forme"} onSubmit={handleSubmitForm}>
          <div className={"mySearch"}>
            <SearchComponent
              color="primary"
              inputValue={inputValue}
              handleInputChange={handleInputChange}
            />
          </div>

          <FormControl color="primary" sx={{ m: 1, minWidth: 80 }}>
            <InputLabel color="primary" id="demo-simple-select-autowidth-label">
              Type
            </InputLabel>
            <Select
              color="primary"
              labelId="demo-simple-select-autowidth-label"
              id="demo-simple-select-autowidth"
              value={type}
              onChange={handleChangeType}
              autoWidth
              label="Type"
            >
              <MenuItem value="">
                <em>all</em>
              </MenuItem>
              <MenuItem value={"movie"}>movie</MenuItem>
              <MenuItem value={"series"}>series</MenuItem>
              <MenuItem value={"episode"}>episode</MenuItem>
            </Select>
          </FormControl>
          <TextField
            color="primary"
            id="outlined-basic"
            label="Year"
            variant="outlined"
            value={yearInputValue}
            onChange={handleYearInputChange}
          />
          {errorYearMsg ? (
            <Alert
              sx={{ position: "fixed", left: "15px", textAlign: "left" }}
              variant="filled"
              severity="warning"
            >
              <AlertTitle>Warning</AlertTitle>
              Year should be empty or more than 1980
            </Alert>
          ) : null}
          <Button
            sx={{ marginLeft: "15px" }}
            color="primary"
            type="submit"
            variant="outlined"
            size="medium"
          >
            Search
          </Button>
        </form>
      </header>

      <div className={"pageDiv"}>
        <div className={"pagination"}>
          <Pagination
            color={"secondary"}
            count={maxPage}
            defaultPage={1}
            page={page}
            onChange={handlePageChange}
          />
        </div>
      </div>
      <Container>
        <MyModal dataFull={dataFull} handleClose={handleClose} open={open} />
        <div className="block">
          {errorMsg ? (
            <Alert className="errorMSG" severity="error">
              {errorMsg}
            </Alert>
          ) : loading ? (
            <p>Loading...</p>
          ) : (
            data.map((element) => {
              return (
                <div
                  style={{ maxWidth: "400px" }}
                  key={element.imdbID}
                  className={"postBlock"}
                >
                  <Card sx={{ width: "100%", flexDirection: "column" }}>
                    <CardActionArea
                      propskey={element.imdbID}
                      onClick={(e) => clickFunction(e, `${element.imdbID}`)}
                      sx={{
                        display: "flex",
                        flexDirection: "column",
                        width: "100%",
                        minHeight: 500,
                        height: 700,
                      }}
                    >
                      {element.Poster !== "N/A" ? (
                        <CardMedia
                          className="poster"
                          component="img"
                          image={element.Poster}
                          height="590"
                          sx={{ width: "100%" }}
                        />
                      ) : (
                        <div
                          className="poster"
                          style={{
                            width: "100%",

                            textAlign: "center",
                            background: "grey",
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                            color: "black",
                          }}
                        >
                          Picture not found.
                        </div>
                      )}

                      <CardContent>
                        <Typography
                          sx={{ fontSize: 20, height: 50 }}
                          gutterBottom
                          variant="h5"
                          component="div"
                        >
                          {element.Title}
                        </Typography>
                        <Typography variant="body2" color="text.disabled">
                          {element.Type} {element.Year}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Card>
                </div>
              );
            })
          )}
        </div>
      </Container>
      <div className={"pageDiv"}>
        <div className={"pagination"}>
          <Pagination
            color={"secondary"}
            count={maxPage}
            defaultPage={1}
            page={page}
            onChange={handlePageChange}
          />
        </div>
      </div>
    </div>
  );
};

export default HomePage;
